<template>
    <div>
        <div class="border-bottom">
            <v-col cols="auto">
                <div class="d-flex align-center justify-end">
                    <v-btn class="primaryBtn mr-2" elevation="0" style="width: auto;" @click="(addUpdateUserModal = true) && (modalType = 'add')">
                        <img src="@/assets/icons/add.svg"
                         alt="export" class="mr-1" width="23">
                        <span class="fs-16px">Qo'shish</span>
                    </v-btn>
                </div>
            </v-col>
        </div>
        <v-data-table
            :headers="headers"
            :items="items"
            item-key="id"
            :loading="loading"
            disable-sort
            hide-default-footer
            class="w-100"
        >
            <!-- eslint-disable  -->
            
            <template v-slot:item.name="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.name }}</span>
                </div>
            </template>
            <template v-slot:item.email="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.email }}</span>
                </div>
            </template>
            <template v-slot:item.position="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.position }}</span>
                </div>
            </template>
            <template v-slot:item.date="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.date }}</span>
                </div>
            </template>
            <template v-slot:item.actions="{item}">
                <div class="d-flex align-center justify-center">
                    <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'warehouse_ip_kirim')">
                        <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer"
                        @click="editUser(item)">
                    </div>
                    <div class="cursor-pointer mt-1" @click="deleteUser(item)" v-if="$can('delete', 'warehouse_ip_kirim')">
                        <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                    </div>
                </div>
            </template>

            <template v-slot:no-data>
                <div>
                    <no-data class="mt-6"></no-data>
                    <br>
                    <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                </div>
            </template>

        </v-data-table>
        
        <v-pagination
            v-model="page"
            :length="pageCount"
            total-visible="7"
            class="my-2"
        ></v-pagination>


        <v-dialog
            max-width="400"
            persistent
            v-model="addUpdateUserModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Foydalanuvchi ma`lumotlari</span>
                    <v-btn icon @click="closeModal">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                <ValidationObserver ref="formAddUpdateUser" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">
                      
                        <div class="mt-4">
                            <span class="text-black ml-1">Ism Sharif</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'name']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="user.name" class="mt-2"
                                    placeholder="Misol: Vilvit" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Lavozim</span>
                            <ValidationProvider :rules="{required: true}"
                              v-slot="{ errors }">
                                <v-select :items="rolesList" v-model="user.position" 
                                    class="mt-2" item-value="id" label="Tanlang"
                                    outlined :error-messages="errors" item-text="name">
                                </v-select>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Login</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'email']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="user.email" class="mt-2"
                                    placeholder="Misol: 666777" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div>
                            <span class="text-black ml-1">Parol</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'password']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="user.password" class="mt-2"
                                    :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                                    :type="show1 ? 'text' : 'password'"
                                    counter
                                    @click:append="show1 = !show1"
                                    placeholder="Misol: bu yashil ip" :error-messages="errors">
                                    
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <div v-if="modalType == 'add'">
                            <span class="text-black ml-1">Parolni tasdiqlash</span>
                            <ValidationProvider :rules="{required: true,
                             error: [errorStatus, errorMessage, errorPlace, 'repeatPassword']}"
                              v-slot="{ errors }">
                                <v-text-field outlined v-model="user.repeatPassword" class="mt-2"
                                    :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                                    :type="show1 ? 'text' : 'password'"
                                    counter
                                    @click:append="show1 = !show1"
                                    placeholder="Misol: bu yashil ip" :error-messages="errors">
                                </v-text-field>
                            </ValidationProvider>
                        </div>

                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeModal"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >{{ modalType == 'add' ? 'Qo\'shish' : 'Tahrirlash'}}
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>


        <DeleteModal :deleteModal="deleteModal" :user_id="one_user.id"
         :actionName="'main/deleteUser'" :modalTitle="'Foydalanuvchi o\'chirilishi'"
         :modalDesc="'Bu foydalanuvchini o‘chirmoqchimisiz?'"></DeleteModal>
    </div>
</template>

<script>
import {mapState, mapActions} from "vuex";
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import NoData from "@/components/base/NoData.vue";
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    props: ['update'],
    mixins: [loaderMixin],
    components:{
        DeleteModal,
        NoData
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            page: 1,
            pageCount: 0,
            loading: false,
            deleteModal: false,
            addUpdateUserModal: false,
            modalType: 'add',
            
            show1: false, 
            user: {
                name: '',
                position: '',
                email: '',
                password: '',
                repeatPassword: '',
            },
            positions: [],
            rolesList: [],
            
            headers: [
                {text: 'Ism Sharif', value: 'name', align: 'center', class: 'table-header'},
                {text: 'Login', value: 'email', align: 'center', class: 'table-header'},
                {text: 'Lavozim', value: 'position', align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'date', align: 'center', class: 'table-header'},
                {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: [],
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        'page': async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        'user.repeatPassword': {
            handler: async function(newValue) {
                if(newValue){
                    if(newValue != this.user.password){
                        
                    }
                }
            },
            deep: true,
        },
        'user': {
            handler: async function(newValue) {
                if(newValue){
                    this.clearErrors();
                }
            },
            deep: true,
        },
        '$store.state.main.users_list': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_USERS_LIST', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    async mounted(){
        await this.refresh();
    },
    computed:{
        ...mapState(
            {
                users: state => state.main.users,
                roles: state => state.main.roles,
                searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue,
                one_user: state => state.main.one_user
            }
        ),
    },
    methods:{
        ...mapActions(
            {
                fetchUsers: 'main/fetchUsers',
                fetchRoles: 'main/fetchRoles',
                postUsers: 'main/postUsers',
                updateUsers: 'main/updateUsers',
                deleteUser: 'main/deleteUser',
                fetchOneUser: 'main/fetchOneUser'
            }
        ),
        async editUser(item){
            this.addUpdateUserModal = true;
            this.modalType = 'edit';

            await this.fetchOneUser(item.id)
            this.user.name = this.one_user.name
            this.user.position = this.one_user.role_id
            this.user.email = this.one_user.email
        },
        async deleteUser(item){
            this.deleteModal = true;
            await this.fetchOneUser(item.id)
        },



        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.user = {
                name: '',
                position: '',
                email: '',
                password: '',
                repeatPassword: '',
            },
            this.clearErrors();
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeModal(){
            this.clearInputs();
            this.$refs.formAddUpdateUser.reset();
            this.addUpdateUserModal = false;
        },
        // ============= CLEARS_AND_CLOSES END =============



        async refresh() {
            this.loading = true
            await this.fetchUsers({page: this.page})
            this.items = this.users.data

            this.page = this.users.current_page;
            this.pageCount = Math.ceil(this.users.total / 10);

            this.items.forEach((element) => {
                element.position = element.role.name
                if(element.created_at)
                element.date = this.$dayjs(element.created_at).format('DD-MM-YYYY') 
            })


            await this.fetchRoles();
            this.rolesList = this.roles
            this.rolesList.splice(0,1);

            this.loading = false
        },


        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formAddUpdateUser.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============

                    if(this.modalType == 'add'){
                        if(this.user.password != this.user.repeatPassword){
                            this.errorMessage = 'Parollar bir biriga mos kelmayapti!';
                            this.errorPlace = 'repeatPassword'
                            this.errorStatus = true;
                            this.$toast.error(this.errorMessage);
                        }else{
                            // ============= ADDING_NEW_USER START =============
                            let a = await this.postUsers({
                                name: this.user.name,
                                email: this.user.email,
                                password: this.user.password,
                                role_id: this.user.position,
                            })
                            
    
                            if (a.response && a.response.status == 422) {
                                if(a.response.data.errors.name){
                                    this.errorMessage = a.response.data.errors.name[0];
                                    this.errorPlace = 'name'
                                }else if(a.response.data.errors.email){
                                    this.errorMessage = a.response.data.errors.email[0];
                                    this.errorPlace = 'email'
                                }else if(a.response.data.errors.password){
                                    this.errorMessage = a.response.data.errors.password[0];
                                    this.errorPlace = 'password'
                                }
                                this.errorStatus = true;
                                this.$toast.error(this.errorMessage);
                            }else if(a.response && a.response.status == 409){
                                if(a.response.data.message){
                                    this.errorMessage = a.response.data.message;
                                }
                                this.$toast.error(this.errorMessage);
                            }else{
                                await this.refresh()
                                this.$toast.success('Muvaffaqiyatli qo`shildi!');
                                this.closeModal();
                            }
                            // ============= ADDING_NEW_USER END =============
                        }
                    }else if(this.modalType == 'edit'){
                        // ============= UPDATING_USER START =============
                        let a = await this.updateUsers({
                            id: this.one_user.id,
                            name: this.user.name,
                            email: this.user.email,
                            password: this.user.password,
                            role_id: this.user.position,
                        })
    
                        if (a.response && a.response.status == 422) {
                            if(a.response.data.errors.name){
                                this.errorMessage = a.response.data.errors.name[0];
                                this.errorPlace = 'name'
                            }else if(a.response.data.errors.password){
                                this.errorMessage = a.response.data.errors.password[0];
                                this.errorPlace = 'password'
                            }
                            this.errorStatus = true;
                            this.$toast.error(this.errorMessage);
                        }else if(a.response && a.response.status == 409){
                            if(a.response.data.message){
                                this.errorMessage = a.response.data.message;
                            }
                            this.$toast.error(this.errorMessage);
                        }else{
                            await this.refresh()
    
                            this.$toast.success('Muvaffaqiyatli qo`shildi!');
                            this.closeModal();
                        }
                        // ============= UPDATING_USER END =============
                    }
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        },
        // ============= ON_SUBMIT END =============
    }
}
</script>

<style>

</style>
